.border-bottom-white {
    border-bottom: 1px solid rgba(83, 78, 78, 0.721);
}
.border-bottom-black {
    border-bottom: 1px solid rgba(200, 192, 192, 0.705);
}


#myBtn {
    display: none;
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 99;
    font-size: 15px;
    border: none;
    outline: none;
    background-color: rgba(223, 213, 213, 0.397);
    color: rgb(7, 7, 7);
    cursor: pointer;
    padding: 10px;
    border-radius: 4px;
  }
  #myBtn:hover {
    background-color: #e0d7d7;
  }


  .form-check-input:checked {
    background-color: rgb(142, 205, 142);
    border-color: pink;
}
.form-switch .form-check-input {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='green'/%3e%3c/svg%3e");
}

.no-copy {
  user-select: none;
}


/* media query navbar */
@media (max-width: 600px) {
  .navbar .flex {
    /* flex-direction: column; */
    align-items: center;
  }

  .navbar .title-font {
    font-size: 1rem;
  }

  .navbar .btn {
    margin: 5px 0;
  }
}

